.listContainer{
  display: flex;
  justify-content: center;
  margin-top: 20px;

}
.listWrapper{
    width: 100%;
    min-width: 1024px;
    display: flex;
    gap: 20px;
}
.listSearch{
    flex: 1;
    background-color: #feb002;
    padding: 10px;
    border-radius: 10px;
    position:sticky;
    top: 10px;
    margin-left: 100px;
    height: max-content;
}
.lsTitle{
    font-size: 20px;
    color: #555;
    margin-bottom: 10px;
}
.lsItem{
    display: flex;
    flex-direction: column;
    gap: 5px;
    margin-bottom: 10px;
}
.lsItem>label{
    font-size: 12px;
}
.lsItem>input{
    height: 30px;
    border: none;
    padding: 5px;
}
.lsOptions{
    padding: 10px;
}
.lsOptionItem{
    display: flex;
    justify-content: space-between;
    margin-bottom:10px;
    color: #555;
    font-size: 12px;
}
.lsOptionInput{
    width: 50px;
}
.listSearch>button{
    padding: 10px;
    background-color: #31b675;
    color: white;
    border: none;
    width: 100%;
    font-weight: 500;
    cursor: pointer;
}
.listResult{
    flex: 3;
}