/* hostel.css */

.hotelContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
}

.hotelwrapper {
  width: 100%;
  min-width: 320px; /* Adjust minimum width as needed */
  display: flex;
  flex-direction: column;
  gap: 10px;
  position: relative;
  margin-left: 20px; /* Adjust margin for better spacing */
}

.bookNow {
  position: absolute;
  top: 10px;
  right: 20px;
  border: none;
  padding: 10px 20px;
  background-color: #31b675;
  color: white;
  font-weight: bold;
  border-radius: 5px;
  cursor: pointer;
}

.hotelTitle {
  font-size: 24px;
}

.hotelAddress {
  font-size: 14px;
  display: flex;
  align-items: center;
  gap: 10px;
}

.hotelDistance {
  color: #0071c2;
  font-weight: 500;
}

.hotelPriceHighlight {
  color: #008009;
  font-weight: 500;
}

.hotelImages {
  display: flex;
  flex-wrap: wrap;
}

.hotelImgWrapper {
  width: calc(33.33% - 10px); /* Adjust the width and spacing */
  margin: 5px;
  overflow: hidden; /* Hide overflow to ensure consistent height */
}

.hotelImg {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.hotelDetails {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  margin-top: 20px;
}

.hotelDetailsTexts {
  flex: 1;
}

.hotelDesc {
  font-size: 14px;
  margin-top: 20px;
}

.hotelDetailsPrice {
  flex: 1;
  background-color: #ebf3ff;
  padding: 20px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.hotelDetailsPrice > h1 {
  font-size: 18px;
  color: #555;
}

.hotelDetailsPrice > span {
  font-size: 14px;
}

.hotelDetailsPrice > h2 {
  font-weight: 300;
}

.hotelDetailsPrice > button {
  border: none;
  padding: 10px 20px;
  background-color: #31b675;
  color: white;
  font-weight: bold;
  cursor: pointer;
  border-radius: 5px;
}

.slider {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5); /* Adjust background opacity */
  z-index: 999;
  display: flex;
  align-items: center;
  justify-content: center;
}

.sliderWrapper {
  width: 80%;
  max-height: 80%;
  overflow: hidden; /* Hide overflow to ensure consistent height */
}

.sliderImg {
  width: 100%;
  height: 100%;
  object-fit: contain; /* Adjust object-fit as needed */
}

.close {
  position: absolute;
  top: 20px;
  right: 20px;
  font-size: 30px;
  color: lightgray;
  cursor: pointer;
}

.arrow {
  margin: 20px;
  font-size: 50px;
  color: lightgray;
  cursor: pointer;
}
