/* dashboard.css */

.dashboard {
  padding: 20px;
}

.dashboard-item {
  background-color: #ffffff;
  padding: 20px;
  margin-bottom: 20px;
  border: 1px solid #ced4da;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

table {
  width: 100%;
  margin-top: 10px;
  border-collapse: collapse;
}

td {
  padding: 8px;
  border-bottom: 1px solid #ced4da;
}

td:first-child {
  font-weight: bold;
  color: #007bff;
}
