body {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
}
.cart {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 20px;
}

.hostel-details {
  border: 1px solid #ccc;
  padding: 20px;
  margin-top: 20px;
  width: 300px;
  text-align: center;
}

.hostel-details h2 {
  margin-bottom: 10px;
}

.hostel-details h4 {
  margin-bottom: 5px;
}

.hostel-details p {
  margin-bottom: 15px;
}

.hostel-details div {
  margin-bottom: 10px;
}

.hostel-details label {
  display: block;
  margin-bottom: 5px;
}

.hostel-details input {
  width: 100%;
  padding: 8px;
  box-sizing: border-box;
  margin-bottom: 10px;
}

.hostel-details button {
  background-color: #4caf50;
  color: white;
  padding: 10px;
  border: none;
  cursor: pointer;
  width: 100%;
}

/* Optional: Add styles for an image on the left */
.cart img {
  max-width: 100%;
  max-height: 200px;
  margin-right: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
}
