/* SearchItem.css */
.search-item {
  display: flex;
  border: 1px solid #ddd;
  border-radius: 8px;
  margin-bottom: 16px;
  overflow: hidden;
  text-decoration: none;
  color: inherit;
  transition: transform 0.2s ease-in-out;
}

.search-item:hover {
  transform: scale(1.02);
}

.siImage {
  flex: 1;
  max-width: 150px;
  overflow: hidden;
}

.siImage img {
  width: 100%;
  height: auto;
  object-fit: cover;
}

.siDetails {
  flex: 2;
  padding: 16px;
}

.siDetails h2 {
  font-size: 1.5rem;
  margin-bottom: 8px;
}

.siDetails p {
  margin-bottom: 4px;
}
.searchItemImage {
  width: 100px; /* Adjust the width as needed */
  height: auto; /* Maintain the aspect ratio */
  margin-right: 10px;
}


