.header{
    background-color:#222222;
    color: white;
    display: flex;
    justify-content: center;
    position: relative;
}
.headerContainer{
    /* width: 100px;
    max-width: 1024px; */
    margin: 20px 0px 50px 0px;
}
.headerContainer.listMode {
    margin: 20px 0px 0px 0px;

}
.headerList{
    display: flex;

}
.headerListItem{
    display: flex;
    align-items: center;
    grid-area: 10px;
}

.headerDesc{
    margin: 20px 0px;
}

.headerBtn{
    background-color: #31b675;
    color: white;
    font-weight: 500;
    border: none;
    padding: 10px;
    cursor: pointer;
}
.headerSearch{
    height: 30px;
    background-color: white;
    border: 3px solid #febb02;
    display: flex;
    align-items: center;
    padding: 10px 0px;
    border-radius: 5px;
    position: absolute;
    bottom: -25px;

}
.headerIcon{
    color: lightgray;
}
.headerSearchItems{
    display: flex;
    align-items: center;
    gap: 10px;

}
.headerSearchInput{
    border: none;
    outline: none;
}
