.hostel-details-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.hostel-details {
  max-width: 800px;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 8px;
  background-color: #fff;
}

.hostel-info {
  display: flex;
  justify-content: space-between;
}

.hostel-info div {
  flex: 1;
  margin-right: 20px;
}

.hostel-images img {
  max-width: 100%;
  margin-top: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
}
